import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";

export interface IndexProps {}
export interface IndexState {
    items: [
        {
            title: string;
            date: string;
            news: string;
            link: string;
            desc: string;
        }
    ];
}
const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    margin-top: 62px;
    & > div {
        display: flex;
        margin-bottom: 34px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
        }
        & > div:nth-child(2) {
            width: 80%;
        }
    }
`;

const Item = styled.a`
    padding-bottom: 34px;
    margin-bottom: 34px;

    border-bottom: 1px solid #000;

    & > div {
        &:nth-child(1) {
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.71;
            letter-spacing: normal;
            color: #000000;

            margin-bottom: 12px;

            & > span {
                font-weight: bold;
            }
        }

        &:nth-child(2) {
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #000000;
            margin-bottom: 12px;
        }

        &:nth-child(3) {
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.71;
            letter-spacing: normal;
            color: #000000;
            overflow: hidden;

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            height: 51px;
        }
    }
`;

export default class Index extends React.Component<IndexProps, any> {
    constructor(props: IndexProps) {
        super(props);
        this.state = {
            items: [
                {
                    title: '"일·거주·레저를 한번에" 소셜공간이 골목 살린다',
                    date: "2018.6.22",
                    news: "머니투데이",
                    link: "http://news.mt.co.kr/mtview.php?no=2018062017083451234",
                    desc:
                        '"일과 주거, 다이닝, 레저를 아우르는 소셜공간이 기대보다 반응이 좋아요. 오래된 건물을 재생하고 골목상권을 살린다는 의미도 크죠."\n 이장호 유니언플레이스 대표(42·사진)는 지난 3월 ‘호텔에 없는 S’라는 슬로건으로 공간 브랜드를 론칭했다.'
                },
                {
                    title: "[마켓인사이트]코람코자산운용-유니언플레이스, 당산동 노후 빌딩 청년 복합 문화 공간으로 탈바꿈",
                    date: "2018.5.9",
                    news: "한국경제",
                    link: "http://marketinsight.hankyung.com/apps.free/free.news.view?aid=201805094681u&category=IB_FREE",
                    desc:
                        "코람코자산운용이 복합 업무공간을 조성하는 스타트업(신생기업) 유니언플레이스와 손잡고 도심의 소형 빌딩을 개조하는 밸류애드(가치부가형) 투자를 한다. 오래된 건물을 청년 창업자가 업무, 거주, 숙박을 해결할 수 있는 공간으로 조성할 계획이다. 도시재생사업에서 부동산 자산운용사가 새로운 먹거리를 찾았다는 평가다."
                },
                {
                    title: "노는 공간의 재탄생…확산되는 소규모 `건물 재생`",
                    date: "2018.4.6",
                    news: "매일경제",
                    link: "http://news.mk.co.kr/newsRead.php?year=2018&no=221488",
                    desc:
                        "노후된 고시원·재수생 기숙사(왼쪽 사진)에서 특색 있는 `코리빙` 호스텔(오른쪽)로 재탄생한 서울 서초구 서초동 1670-15 일대 전경. 사진설명노후된 고시원·재수생 기숙사(왼쪽 사진)에서 특색 있는 `코리빙` 호스텔(오른쪽)로 재탄생한 서울 서초구 서초동 1670-15 일대 전경. 방치된 노후 건물을 특색 있는 공간으로 재구성하는 소규모 `건물 재생`이 확산되고 있다. 6일 부동산 업계에 따르면 최근 서울 지하철 2·3호선 교대역 인근 영남빌딩(서초동 1670-15)은 30년 넘은 옛 고시원·재수생 기숙사에서 호스텔 `업플로`로 재탄생했다. 리모델링 전 영남빌딩은 공실과 노후화에 시달리고 있었다. 통상 호스텔은 관광객이 잠만 자고 떠나는 숙박시설로 활용되지만 업플로는 거주, 업무, 여가를 한곳에서 즐길 수 있는 `코리빙(co-living)` 공간을 제공한다."
                },
                {
                    title: "국내 최초의 소셜라이징 호스텔, 업플로호스텔 런칭",
                    date: "2018.4.2",
                    news: "오센",
                    link: "http://www.osen.co.kr/article/G1110869235",
                    desc:
                        "공유공간 서비스 기업이자 도시재생 스타트업인 유니언플레이스가 국내 최초의 소셜라이징 호스텔 브랜드 ‘업플로호스텔(upflo hostel)’을 런칭했다. 낙후된 도심 속 건물을 리모델링해 내국인들 뿐만 아니라 외국 관광객들까지 끌어들여 지속 가능한 지역 상권을 만들겠다는 전략이다. 실제로 업플로호스텔이 있는 ‘영남빌딩’은 1988년에 건축되어 30년 만에 새로운 건물로 재탄생 됐다."
                },
                {
                    title: "'도시재생 스타트업' 유니언플레이스, 소셜라이징 호스텔 '업플로호스텔' 런칭",
                    date: "2018.4.2",
                    news: "문화뉴스",
                    link: "http://www.munhwanews.com/news/articleView.html?idxno=127415",
                    desc:
                        "공유공간 서비스 기업이자 도시재생 스타트업인 유니언플레이스(대표 이장호)가 소셜라이징 호스텔 브랜드 ‘업플로호스텔(upflo hostel)’을 런칭했다고 2일 밝혔다. 낙후된 도심 속 건물을 리모델링해 내국인들 뿐만 아니라 외국 관광객들까지 끌어들여 지속 가능한 지역 상권을 만들겠다는 전략이다. 실제로 업플로호스텔이 있는 ‘영남빌딩’은 1988년에 건축되어 30년 만에 새로운 건물로 재탄생 되었다."
                },
                {
                    title: "청년들의 꿈·기회 되살리는 인큐베이터 역할 하고 싶어",
                    date: "2018.3.28",
                    news: "파이낸셜뉴스",
                    link: "http://www.fnnews.com/news/201803281659083590",
                    desc:
                        "최근 서울 서초동 서울교육대학교 인근에 눈에 띄는 건물이 하나 들어섰다. 벽면에 'upflo(업플로)'라고 적힌 5층 높이의 현대식 빌딩은, 30년이 넘은 낡은 건물을 리모델링해 문을 열었다. 이 건물이 주목을 받은 것은 단순히 외향 때문 만은 아니다."
                },
                {
                    title: "[도시재생 스타트]①세운상가의 변신, 반려견 로봇이 '뚝딱'",
                    date: "2018.3.28",
                    news: "뉴스1",
                    link: "http://news1.kr/articles/?3271627",
                    desc:
                        "최근 낙후된 도심 속 건물을 리모델링해 지속 가능한 상권을 살리겠다는 도시재생도 나타났다. 도시재생 스타트업 기업인 유니언플레이스는 지하철 교대역 인근 건물을 매입해 호텔과 게스트하우스 개념을 결합한 새로운 상품을 선보인다. \n1988년 들어선 이곳은 애초 인근 학원생들의 학사(기숙사)였다. 하지만 활용도가 떨어지면서 새로운 사업 모델이 필요했다."
                }
            ]
        };
    }
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "홍보", link: "/promotion" }, { name: "보도자료", link: "/promotion" }]}
                />
                <Content>
                    <Desc>
                        <div>
                            <div>보도자료</div>
                            <div>
                                {this.state.items.map((item, idx) => (
                                    <Item href={item.link} key={idx}>
                                        <div>
                                            <span>{item.news}</span> ㅣ {item.date}
                                        </div>
                                        <div>{item.title}</div>
                                        <div>
                                            {item.desc.split("\n").map((s: string) => (
                                                <span>
                                                    {s}
                                                    <br />
                                                </span>
                                            ))}
                                        </div>
                                    </Item>
                                ))}
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



